import React from "react";
import PopupWithForm from "./PopupWithForm";
import CurrentUserContext from "../contexts/CurrentUserContext";

function EditProfilePopup({ isOpen, onClose, onUpdateUser, isLoading }) {
  const [name, setName] = React.useState("");
  const [description, setDescription] = React.useState("");
  const currentUser = React.useContext(CurrentUserContext);

  React.useEffect(() => {
    setTimeout(() => {
      setName(currentUser.name);
      setDescription(currentUser.about);
    }, 200);
  }, [currentUser, isOpen]);

  function handleNameChange(e) {
    setName(e.target.value);
  }

  function handleDescriptionChange(e) {
    setDescription(e.target.value);
  }

  function handleSubmit(e) {
    e.preventDefault();
    onUpdateUser({
      name,
      about: description,
    });
  }

  return (
    <PopupWithForm
      onSubmit={handleSubmit}
      name="pop-up_profile"
      title="–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –ø—Ä–æ—Ñ–∏–ª—å"
      isOpen={isOpen}
      onClose={onClose}
    >
      <div className="form__input-container">
        <input
          name="input-name"
          type="text"
          className="form__input form__input_name"
          placeholder="–ò–º—è –§–∞–º–∏–ª–∏—è"
          required
          minLength="2"
          maxLength="40"
          pattern="[A-Za-z–ê-–Ø–∞-—è–Å—ë\s\-]+"
          onChange={handleNameChange}
          value={name}
        />
        <span id="input-name-error" className="form__input-error">
          –û—à–∏–±–∫–∞
        </span>
      </div>
      <div className="form__input-container">
        <input
          name="input-profession"
          type="text"
          className="form__input form__input_profession"
          placeholder="–ü—Ä–æ—Ñ–µ—Å—Å–∏–æ–Ω–∞–ª—å–Ω–∞—è –¥–µ—è—Ç–µ–ª—å–Ω–æ—Å—Ç—å"
          required
          minLength="2"
          maxLength="200"
          onChange={handleDescriptionChange}
          value={description}
        />
        <span id="input-profession-error" className="form__input-error">
          –û—à–∏–±–∫–∞
        </span>
      </div>
      <button type="submit" className="form__input-button">
        {isLoading ? "–ó–∞–≥—Ä—É–∑–∫–∞..." : "–°–æ—Ö—Ä–∞–Ω–∏—Ç—å"}
      </button>
    </PopupWithForm>
  );
}

export default EditProfilePopup;
