import React from "react";
import PopupWithForm from "./PopupWithForm";

function AddPlacePopup({ isOpen, onClose, onAddPlace, isLoading }) {
  const [name, setName] = React.useState("");
  const [link, setLink] = React.useState("");
  const formElement = React.useRef();

  function handleNameChange(e) {
    setName(e.target.value);
  }

  function handleLinkChange(e) {
    setLink(e.target.value);
  }

  function handleAddPlaceSubmit(e) {
    e.preventDefault();
    onAddPlace({ name, link });
  }

  React.useEffect(() => {
    setTimeout(() => {
      setName("");
      setLink("");
    }, 200);
  }, [isOpen]);

  return (
    <PopupWithForm
      ref={formElement}
      onSubmit={handleAddPlaceSubmit}
      name="pop-up_card"
      title="–ù–æ–≤–æ–µ –º–µ—Å—Ç–æ"
      isOpen={isOpen}
      onClose={onClose}
    >
      <div className="form__input-container">
        <input
          name="input-name-card"
          type="text"
          className="form__input form__input_name-card"
          placeholder="–ù–∞–∑–≤–∞–Ω–∏–µ"
          required
          minLength="1"
          maxLength="30"
          pattern="[A-Za-z–ê-–Ø–∞-—è–Å—ë\s\-]+"
          onChange={handleNameChange}
          value={name}
        />
        <span id="input-name-card-error" className="form__input-error">
          –û—à–∏–±–∫–∞
        </span>
      </div>
      <div className="form__input-container">
        <input
          name="input-link-card"
          type="url"
          className="form__input form__input_link-card"
          placeholder="–°—Å—ã–ª–∫–∞ –Ω–∞ –∫–∞—Ä—Ç–∏–Ω–∫—É"
          required
          onChange={handleLinkChange}
          value={link}
        />
        <span id="input-link-card-error" className="form__input-error">
          –û—à–∏–±–∫–∞
        </span>
      </div>
      <button type="submit" className="form__input-button">
        {isLoading ? "–ó–∞–≥—Ä—É–∑–∫–∞..." : "–°–æ–∑–¥–∞—Ç—å"}
      </button>
    </PopupWithForm>
  );
}

export default AddPlacePopup;
